/*==================================
    Banner One
====================================*/
.main-slider-one {
  position: relative;
  display: block;
  z-index: 1;
}

.main-slider-one .image-layer {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-attachment: scroll;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-transform: scale(1);
  transform: scale(1);
  -webkit-transition: -webkit-transform 7500ms ease;
  transition: -webkit-transform 7500ms ease;
  transition: transform 7500ms ease;
  transition: transform 7500ms ease, -webkit-transform 7500ms ease;
  z-index: 1;
}

.main-slider.main-slider-one .swiper-slide-active .image-layer {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.main-slider-one .big-title {
  position: absolute;
  bottom: 20px;
  right: 10px;
  opacity: 0;
  -webkit-transition: all 1000ms ease;
  transition: all 1000ms ease;
  -webkit-transform: translateY(80px);
  transform: translateY(80px);
  z-index: 3;
}

.main-slider.main-slider-one .swiper-slide-active .big-title {
  opacity: 1;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  -webkit-transition-delay: 1000ms;
  transition-delay: 1000ms;
}

.main-slider-one .big-title h2 {
  color: transparent;
  -webkit-text-stroke: 2px rgba(255, 255, 255, 0.2);
  font-size: 100px;
  line-height: 1em;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 11.5px;
}

.main-slider-one .img-box {
  position: absolute;
  right: 0;
  bottom: 0;
  opacity: 0;
  -webkit-transition: all 1200ms ease;
  transition: all 1200ms ease;
  -webkit-transform: translateX(80px);
  transform: translateX(80px);
  z-index: 1;
}

.main-slider.main-slider-one .swiper-slide-active .img-box {
  opacity: 0.6;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  -webkit-transition-delay: 1300ms;
  transition-delay: 1300ms;
}

.main-slider-one .shape1 {
  position: absolute;
  top: 0px;
  right: 0px;
  z-index: 1;
}

.main-slider-one .icon-one {
  position: absolute;
  top: 125px;
  left: 74px;
  z-index: 2;
}

.main-slider-one .icon-two {
  position: absolute;
  bottom: 125px;
  left: 74px;
  z-index: 2;
}

.main-slider-one__single {
  position: relative;
  display: block;
  padding: 220px 0px 120px;
}

.main-slider-one__content {
  position: relative;
  display: block;
  padding-top: 43px;
  padding-left: 55px;
  padding-bottom: 50px;
  max-width: 690px;
  width: 100%;
  z-index: 1;
}

.main-slider-one__content::before {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0px;
  width: 10%;
  background: #000000;
  mix-blend-mode: soft-light;
  border-radius: 10px;
  opacity: 0;
  -webkit-transform: perspective(400px) rotateY(0deg) translateX(0px);
  -ms-transform: perspective(400px) rotateY(0deg) translateX(0px);
  transform: perspective(400px) rotateY(0deg) translateX(0px);
  -webkit-transform-origin: bottom;
  -ms-transform-origin: bottom;
  transform-origin: bottom;
  -webkit-transition: all 1300ms ease;
  -moz-transition: all 1300ms ease;
  -ms-transition: all 1300ms ease;
  -o-transition: all 1300ms ease;
  transition: all 1300ms ease;
  content: "";
  z-index: -1;
}

.main-slider .swiper-slide-active .main-slider-one__content::before {
  opacity: 0.2;
  -webkit-transform: perspective(400px) rotateY(0deg) translateX(0px);
  -ms-transform: perspective(400px) rotateY(0deg) translateX(0px);
  transform: perspective(400px) rotateY(0deg) translateX(0px);
  -webkit-transition-delay: 300ms;
  -moz-transition-delay: 300ms;
  -ms-transition-delay: 300ms;
  -o-transition-delay: 300ms;
  transition-delay: 300ms;
  width: 62%;
}

.main-slider-one__content h3 {
  color: transparent;
  -webkit-text-stroke: 1px rgba(var(--thm-primary-rgb), 0.8);
  font-size: 24px;
  line-height: 34px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 9.6px;
  margin-bottom: 15px;
  opacity: 0;
  -webkit-transition: all 1000ms ease;
  transition: all 1000ms ease;
  -webkit-transform: translateY(80px);
  transform: translateY(80px);
}

.main-slider .swiper-slide-active .main-slider-one__content h3 {
  opacity: 1;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  -webkit-transition-delay: 1000ms;
  transition-delay: 1000ms;
}

.main-slider-one__content h3 span {
  letter-spacing: 0px;
}

.main-slider-one__content h2 {
  color: var(--thm-white);
  font-size: 90px;
  line-height: 1em;
  letter-spacing: -1.8px;
  margin-bottom: 30px;
  opacity: 0;
  -webkit-transition: all 1500ms ease;
  transition: all 1500ms ease;
  -webkit-transform: translateY(80px);
  transform: translateY(80px);
}

.main-slider .swiper-slide-active .main-slider-one__content h2 {
  opacity: 1;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  -webkit-transition-delay: 1300ms;
  transition-delay: 1300ms;
}

.main-slider-one__content h2 span {
  color: var(--thm-primary);
}

.main-slider-one__content p {
  color: var(--thm-white);
  font-size: 20px;
  font-weight: 500;
  line-height: 32px;
  opacity: 0;
  -webkit-transition: all 1500ms ease;
  transition: all 1500ms ease;
  -webkit-transform: translateY(80px);
  transform: translateY(80px);
}

.main-slider .swiper-slide-active .main-slider-one__content p {
  opacity: 1;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  -webkit-transition-delay: 1600ms;
  transition-delay: 1600ms;
}

.main-slider-one__content .btn-box {
  position: relative;
  display: block;
  margin-top: 41px;
  opacity: 0;
  -webkit-transition: all 1500ms ease;
  transition: all 1500ms ease;
  -webkit-transform: translateY(80px);
  transform: translateY(80px);
}

.main-slider .swiper-slide-active .main-slider-one__content .btn-box {
  opacity: 1;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  -webkit-transition-delay: 1800ms;
  transition-delay: 1800ms;
}

.main-slider-one__content .btn-box .thm-btn::before {
  background: var(--thm-white);
}

.main-slider-one__content .btn-box .thm-btn:hover {
  color: var(--thm-secondary);
}

.main-slider-one__content .btn-box .thm-btn span {
  margin-left: 3px;
  top: 2px;
}

.main-slider__nav {
  position: absolute;
  display: flex;
  align-items: center;
  bottom: 115px;
  right: 140px;
  z-index: 100;
}

.main-slider__nav .swiper-button-next,
.main-slider__nav .swiper-button-prev {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  color: #000000;
  font-size: 15px;
  background: var(--thm-white);
  border: none;
  margin: 0px 0px;
  text-align: center;
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease;
  border-radius: 50%;
  z-index: 100;
}

.main-slider__nav .swiper-button-next:hover,
.main-slider__nav .swiper-button-prev:hover {
  background: var(--thm-primary);
  border: none;
}

.main-slider__nav .swiper-button-next::after,
.main-slider__nav .swiper-button-prev::after {
  display: none;
}

.main-slider__nav .swiper-button-next {
  margin-left: 15px;
}

#main-slider-pagination {
  position: absolute;
  top: 50%;
  left: 60px;
  width: 40px;
  transform: translateY(-50%);
  counter-reset: count;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  line-height: 0;
  z-index: 999999;
}

#main-slider-pagination .swiper-pagination-bullet {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  opacity: 1;
  background-color: transparent;
  -webkit-transition: all 200ms linear;
  transition: all 200ms linear;
  -webkit-transition-delay: 0.1s;
  transition-delay: 0.1s;
}

#main-slider-pagination .swiper-pagination-bullet:before {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  height: 40px;
  text-align: center;
  color: rgba(255, 255, 255, 0.3);
  font-size: 16px;
  line-height: 40px;
  font-weight: 700;
  border: 0px solid transparent;
  counter-increment: count;
  content: "" counter(count);
  font-family: var(--thm-body-font);
  -webkit-transition: all 200ms linear;
  transition: all 200ms linear;
  -webkit-transition-delay: 0.1s;
  transition-delay: 0.1s;
}

#main-slider-pagination .swiper-pagination-bullet + .swiper-pagination-bullet {
  margin-left: 0px;
}

.swiper-container-horizontal
  .swiper-pagination-bullets
  .swiper-pagination-bullet {
  margin: 8px 0px;
}

#main-slider-pagination
  .swiper-pagination-bullet.swiper-pagination-bullet-active:before {
  color: var(--thm-secondary);
}

#main-slider-pagination
  .swiper-pagination-bullet.swiper-pagination-bullet-active {
  border-color: var(--thm-primary);
  background: var(--thm-primary);
}

#main-slider-pagination
  .swiper-pagination-bullet.swiper-pagination-bullet-active:focus,
#main-slider-pagination
  .swiper-pagination-bullet.swiper-pagination-bullet-active:hover {
  outline: none;
  border-color: var(--thm-primary);
  background: var(--thm-primary);
}

